import { Entity } from './entity';

export class VinculacionLaboral extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  private _nombre: string;

  static parse(json: any): VinculacionLaboral[] {
    return json.VinculacionLaboral.map((item: any) => {
      return new VinculacionLaboral(item);
    });
  }

  constructor(json: any) {
    super(json.PK_VinculacionLaboral);
    this._nombre = json.Descripcion_VinculacionLaboral;
  }
}
