import { Entity } from './entity';

export class Municipio extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  private _nombre: string;

  static parse(json: any[]): Municipio[] {
    return json.map((item: any) => new Municipio(item));
  }

  constructor(json: any) {
    super(json.IdMunicipio);
    this._nombre = json.NombreMunicipio;
  }
}
