import { Entity } from './entity';

export class TipoIncidente extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  private _nombre: string;

  static parse(json: any): TipoIncidente[] {
    return json.TiposIncidente.map((item: any) => {
      return new TipoIncidente(item);
    });
  }

  constructor(json: any) {
    super(json.Pk_Id_Tipo_Incidente);
    this._nombre = json.Nombre_Incidente;
  }
}
