import { Entity } from './entity';

export class Jornada extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  private _nombre: string;

  static parse(json: any): Jornada[] {
    return json.Jornadas.map((item: any) => new Jornada(item));
  }

  constructor(json: any) {
    super(json.Pk_Id_Tipo_Jornada);
    this._nombre = json.Nombre_Jornada;
  }
}
