import { Entity } from './entity';

export class ConsecuenciaIncidente extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  private _nombre: string;

  static parse(json: any): ConsecuenciaIncidente[] {
    return json.IncidenteConsecuencias.map((item: any) => {
      return new ConsecuenciaIncidente(item);
    });
  }

  constructor(json: any) {
    super(json.Pk_Id_Incidente_Consecuencia);
    this._nombre = json.Nombre_consecuencia;
  }
}
