import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { MONTHS_SHORT } from 'src/app/commons/locale';
import { alertCommunicationError } from 'src/app/commons/user-feedback';
import { ConsecuenciaIncidente } from 'src/app/entities/consecuencia-incidente';
import { Departamento } from 'src/app/entities/departamento';
import { Entity } from 'src/app/entities/entity';
import { Incidente } from 'src/app/entities/incidente';
import { Jornada } from 'src/app/entities/jornada';
import { Municipio } from 'src/app/entities/municipio';
import { Sede } from 'src/app/entities/sede';
import { Sitio } from 'src/app/entities/sitio';
import { TipoIncidente } from 'src/app/entities/tipo-incidente';
import { VinculacionLaboral } from 'src/app/entities/vinculacion-laboral';
import { Zona } from 'src/app/entities/zona';
import { ContextService } from 'src/app/services/context.service';
import { EmpresaService } from 'src/app/services/empresa.service';
import { IncidenteService } from 'src/app/services/incidente.service';
import { TerritorioService } from 'src/app/services/territorio.service';
import { UtilsService } from 'src/app/services/utils.service';

enum WHICH_DATE {
  MIN,
  MAX,
  NOW
}

@Component({
  selector: 'app-incidentes',
  templateUrl: './incidentes.page.html',
  styleUrls: ['./incidentes.page.scss'],
})
export class IncidentesPage implements OnInit {

  sedes: Sede[];

  vinculacionesLaborales: VinculacionLaboral[];

  zonas: Zona[] = [
    new Zona(1, 'Urbano')
    , new Zona(2, 'Rural')
  ];

  jornadas: Jornada[];

  departamentos: Departamento[];

  municipios: Municipio[];

  tipos: TipoIncidente[];

  sitios: Sitio[];

  consecuencias: ConsecuenciaIncidente[];

  fechaOcurrencia: string = (new Date()).toISOString();

  WHICH_DATE_ENUM = WHICH_DATE;

  formGroup: FormGroup;

  opcion: string;

  compareEntities = Entity.compare;

  readonly MONTH_SHORT_NAMES = MONTHS_SHORT;

  private sedesSubscription: Subscription;

  private listasAuxiliaresSubscription: Subscription;

  private departamentosSubscription: Subscription;

  private municipiosSubscription: Subscription;

  private reporteIncidenteSubscription: Subscription;

  private submitAttempt: boolean;

  constructor(
    private formBuilder: FormBuilder,
    private context: ContextService,
    private empresaService: EmpresaService,
    private territorioService: TerritorioService,
    private incidenteService: IncidenteService,
    private alertCtrl: AlertController,
    private utils: UtilsService
  ) {
    this.formGroup = formBuilder.group({
      sedeFormControl: ['', Validators.compose([Validators.required])],
      vinculacionLaboralFormControl: ['', Validators.compose([Validators.required])],
      zonaTrabajadorFormControl: ['', Validators.compose([Validators.required])],
      fechaFormControl: ['', Validators.compose([Validators.required])],
      horaFormControl: ['', Validators.compose([Validators.required])],
      jornadaFormControl: ['', Validators.compose([Validators.required])],
      jornadaNormalFormControl: ['', Validators.compose([Validators.required])],
      laborFormControl: ['', Validators.compose([Validators.required])],
      laborNoHabitualFormControl: ['', Validators.compose([])],
      tiempoPrevioLaborandoFormControl: ['', Validators.compose([Validators.required])],
      departamentoFormControl: ['', Validators.compose([Validators.required])],
      municipioFormControl: ['', Validators.compose([Validators.required])],
      tipoFormControl: ['', Validators.compose([Validators.required])],
      zonaIncidenteFormControl: ['', Validators.compose([Validators.required])],
      lugarFormControl: ['', Validators.compose([Validators.required])],
      sitioFormControl: ['', Validators.compose([Validators.required])],
      sitioOtroFormControl: ['', Validators.compose([])],
      consecuenciaFormControl: ['', Validators.compose([Validators.required])],
      descripcionFormControl: ['', Validators.compose([Validators.required, Validators.maxLength(2000)])]
    });
  }

  ngOnInit(): void {
    const getData = async (nit: string): Promise<void> => {
      await this.utils.presentLoading();
      this.sedesSubscription = this.empresaService.getSedes(nit).subscribe(
        (response: any) => {
          this.sedes = Sede.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
      await this.utils.presentLoading();
      this.listasAuxiliaresSubscription = this.incidenteService.getListasAuxiliares().subscribe(
        (response: any) => {
          this.vinculacionesLaborales = VinculacionLaboral.parse(response);
          this.jornadas = Jornada.parse(response);
          this.tipos = TipoIncidente.parse(response);
          this.sitios = Sitio.parse(response);
          this.consecuencias = ConsecuenciaIncidente.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
      await this.utils.presentLoading();
      this.departamentosSubscription = this.territorioService.getDepartamentos().subscribe(
        (response: any) => {
          this.departamentos = Departamento.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
    };

    this.context.get().then(c => {
      const nit: string = c.empresa.id.toString();
      getData(nit);
    });
  }

  ionViewWillEnter(): void {
    this.opcion = 'incidents';
  }

  ionViewDidLeave(): void {
    this.opcion = undefined;
  }

  async onDepartamentoChanged(departamento): Promise<void> {
    if (!departamento) { return; }

    await this.utils.presentLoading();
    this.municipiosSubscription = this.territorioService.getMunicipios(departamento.id)
      .subscribe(
        (response: any) => {
          this.municipios = Municipio.parse(response); console.log(this.municipios);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
  }

  isSedeInvalid(): boolean {
    return !this.formGroup.controls.sedeFormControl.valid
      && this.submitAttempt;
  }

  isVinculacionLaboralInvalid(): boolean {
    return !this.formGroup.controls.vinculacionLaboralFormControl.valid
      && this.submitAttempt;
  }

  isZonaTrabajadorInvalid(): boolean {
    return !this.formGroup.controls.zonaTrabajadorFormControl.valid
      && this.submitAttempt;
  }

  isFechaInvalid(): boolean {
    return !this.formGroup.controls.fechaFormControl.valid
      && this.submitAttempt;
  }

  isHoraInvalid(): boolean {
    return !this.formGroup.controls.horaFormControl.valid
      && this.submitAttempt;
  }

  isJornadaInvalid(): boolean {
    return !this.formGroup.controls.jornadaFormControl.valid
      && this.submitAttempt;
  }

  isJornadaNormalInvalid(): boolean {
    return !this.formGroup.controls.jornadaNormalFormControl.valid
      && this.submitAttempt;
  }

  isLaborInvalid(): boolean {
    return !this.formGroup.controls.laborFormControl.valid
      && this.submitAttempt;
  }

  isLaborNoHabitualInvalid(): boolean {
    return !this.formGroup.controls.laborNoHabitualFormControl.valid
      && this.submitAttempt;
  }

  isTiempoPrevioLaborandoInvalid(): boolean {
    return !this.formGroup.controls.tiempoPrevioLaborandoFormControl.valid
      && this.submitAttempt;
  }

  isDepartamentoInvalid(): boolean {
    return !this.formGroup.controls.departamentoFormControl.valid
      && this.submitAttempt;
  }

  isMunicipioInvalid(): boolean {
    return !this.formGroup.controls.municipioFormControl.valid
      && this.submitAttempt;
  }

  isTipoInvalid(): boolean {
    return !this.formGroup.controls.tipoFormControl.valid
      && this.submitAttempt;
  }

  isZonaIncidenteInvalid(): boolean {
    return !this.formGroup.controls.zonaIncidenteFormControl.valid
      && this.submitAttempt;
  }

  isLugarInvalid(): boolean {
    return !this.formGroup.controls.lugarFormControl.valid
      && this.submitAttempt;
  }

  isSitioInvalid(): boolean {
    return !this.formGroup.controls.sitioFormControl.valid
      && this.submitAttempt;
  }

  isSitioOtroInvalid(): boolean {
    return !this.formGroup.controls.sitioOtroFormControl.valid
      && this.submitAttempt;
  }

  isConsecuenciaInvalid(): boolean {
    return !this.formGroup.controls.consecuenciaFormControl.valid
      && this.submitAttempt;
  }

  isDescripcionInvalid(): boolean {
    return !this.formGroup.controls.descripcionFormControl.valid
      && this.submitAttempt;
  }

  isLaborNoHabitual(): boolean {
    return this.formGroup.controls.laborFormControl.value === 'No';
  }

  isSitioOtro(): boolean {
    return this.formGroup.controls.sitioFormControl.value.isOtro === true;
  }

  async reportar(): Promise<void> {
    this.submitAttempt = true;
    if (!this.formGroup.valid) {
      this.alert('Alerta', '', 'Los campos señalados son requeridos.');
      return;
    }

    await this.utils.presentLoading();
    this.incidenteService.postIncidente(await this.getDataFromInputs()).subscribe(
      (response: any) => {
        this.alert('Confirmación', '', 'Se ha generado el reporte exitosamente.');
        this.submitAttempt = false;
        this.formGroup.markAsPristine();
        this.cleanInputs();
        this.utils.dismissLoading();
      },
      (error: any) => {
        alertCommunicationError(this.alertCtrl);
        this.utils.dismissLoading();
      }
    );
  }

  getDate(whichDate: WHICH_DATE): string {
    const date = new Date();
    switch (whichDate) {
      case WHICH_DATE.MIN:
        return '' + (date.getFullYear() - 1);
      case WHICH_DATE.MAX:
      case WHICH_DATE.NOW:
        return date.getFullYear()
          + '-' + ('0' + (date.getMonth() + 1)).slice(-2)
          + '-' + ('0' + date.getDate()).slice(-2);
    }
  }

  async alert(title: string, subTitle: string, message: string): Promise<HTMLIonAlertElement> {
    const alert = await this.alertCtrl.create({
      header: title,
      subHeader: subTitle,
      message,
      buttons: [
        { text: 'Aceptar', role: 'null' }
      ],
      backdropDismiss: true
    });

    await alert.present();

    return alert;
  }

  async getDataFromInputs(): Promise<Incidente> {
    return this.context.get().then(c => {
      const incidente: Incidente = new Incidente();
      incidente.sede = this.formGroup.controls.sedeFormControl.value;
      incidente.vinculacionLaboral = this.formGroup.controls.vinculacionLaboralFormControl.value;
      incidente.idTrabajador = c.trabajador.id;
      incidente.idEmpresa = c.empresa.id;
      incidente.zonaTrabajador = this.formGroup.controls.zonaTrabajadorFormControl.value;
      const fecha = this.fechaOcurrencia.split('T')[0].split('-');
      const hora = this.formGroup.controls.horaFormControl.value.split('T')[1].split(':');
      incidente.fechaHora = new Date(parseInt(fecha[0], 10), parseInt(fecha[1], 10) - 1, parseInt(fecha[2], 10), hora[0], hora[1]);
      incidente.jornada = this.formGroup.controls.jornadaFormControl.value;
      incidente.jornadaNormal = this.formGroup.controls.jornadaNormalFormControl.value;
      incidente.flagLaborHabitual = this.formGroup.controls.laborFormControl.value;
      incidente.incidenteNoLaborHabitual = (this.isLaborNoHabitual())
        ? this.formGroup.controls.laborNoHabitualFormControl.value
        : '';
      const tiempoPrevioLaborando = this.formGroup.controls.tiempoPrevioLaborandoFormControl.value.split('T')[1].split(':');
      incidente.tiempoPrevioLaborando = new Date(0, 0, 0, tiempoPrevioLaborando[0], tiempoPrevioLaborando[1]);
      incidente.departamento = this.formGroup.controls.departamentoFormControl.value;
      incidente.municipio = this.formGroup.controls.municipioFormControl.value;
      incidente.tipo = this.formGroup.controls.tipoFormControl.value;
      incidente.zonaIncidente = this.formGroup.controls.zonaIncidenteFormControl.value;
      incidente.consecuencia = this.formGroup.controls.consecuenciaFormControl.value;
      incidente.lugarIncidente = this.formGroup.controls.lugarFormControl.value;
      incidente.sitioIncidente = this.formGroup.controls.sitioFormControl.value;
      incidente.sitioOtro = (this.isSitioOtro())
        ? this.formGroup.controls.sitioOtroFormControl.value
        : '';
      incidente.descripcion = this.formGroup.controls.descripcionFormControl.value;
      return incidente;
    });
  }

  cleanInputs(): void {
    this.formGroup.controls.sedeFormControl.setValue('');
    this.formGroup.controls.vinculacionLaboralFormControl.setValue('');
    this.formGroup.controls.zonaTrabajadorFormControl.setValue('');
    this.formGroup.controls.fechaFormControl.setValue('');
    this.formGroup.controls.horaFormControl.setValue('');
    this.formGroup.controls.jornadaFormControl.setValue('');
    this.formGroup.controls.jornadaNormalFormControl.setValue('');
    this.formGroup.controls.laborFormControl.setValue('');
    this.formGroup.controls.laborNoHabitualFormControl.setValue('');
    this.formGroup.controls.tiempoPrevioLaborandoFormControl.setValue('');
    this.formGroup.controls.departamentoFormControl.setValue('');
    this.formGroup.controls.municipioFormControl.setValue('');
    this.formGroup.controls.tipoFormControl.setValue('');
    this.formGroup.controls.zonaIncidenteFormControl.setValue('');
    this.formGroup.controls.consecuenciaFormControl.setValue('');
    this.formGroup.controls.lugarFormControl.setValue('');
    this.formGroup.controls.sitioFormControl.setValue('');
    this.formGroup.controls.sitioOtroFormControl.setValue('');
    this.formGroup.controls.descripcionFormControl.setValue('');
  }

}
