import { Entity } from './entity';

export class Sitio extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  get isOtro(): boolean {
    return this._isOtro;
  }

  set isOtro(value: boolean) {
    this._isOtro = value;
  }

  private _nombre: string;

  private _isOtro: boolean;

  static parse(json: any): Sitio[] {
    return json.SitiosIncidente.map((item: any) => new Sitio(item));
  }

  constructor(json: any) {
    super(json.Pk_Id_Sitio_Incidente);
    this._nombre = json.Nombre_Sitio;
    this._isOtro = json.EsOtro;
  }
}
